
.listview {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #222;
}
.listview .list-group {
  padding-bottom: 30px;
}
.listview .list-group .list-group-title {
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
  font-size: 12px;
  color: rgba(255,255,255,0.5);
  background: #333;
}
.listview .list-group .list-group-item {
  display: flex;
  align-items: center;
  padding: 20px 0 0 30px;
}
.listview .list-group .list-group-item .avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.listview .list-group .list-group-item .name {
  margin-left: 20px;
  color: rgba(255,255,255,0.5);
  font-size: 14px;
}
.listview .list-shortcut {
  position: absolute;
  z-index: 30;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  padding: 20px 0;
  border-radius: 10px;
  text-align: center;
  background: rgba(0,0,0,0.3);
  font-family: Helvetica;
}
.listview .list-shortcut .item {
  padding: 3px;
  line-height: 1;
  color: rgba(255,255,255,0.5);
  font-size: 12px;
}
.listview .list-shortcut .item.current {
  color: #ffcd32;
}
.listview .list-fixed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.listview .list-fixed .fixed-title {
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
  font-size: 12px;
  color: rgba(255,255,255,0.5);
  background: #333;
}
.listview .loading-container {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}


